.homepage {
  display:flex;
  width:800px;
  flex-direction:column;
  justify-content: space-around;
  align-items: center;
  padding:5px;
  background:lightgrey;
}

.homepage>* {
  margin-bottom:10px;
}

.auth-img {
  width:400px;
  height:400px;
}

.author-of-the-day {
  text-align: center;
}


.developer {
  margin: 10px 20px;
  padding: 10px;
  background-color: rgb(143, 140, 140);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.developer a {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 19px;
  color: rgb(0, 0, 0);
  text-decoration: none;
}

.developer img {
  margin-bottom: 10px;
}

.developer a:hover {
  color: rgb(243, 243, 243);
}
.about-us {
  width: 80%;
  padding: 10px;  
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(209, 207, 207);
}

.our-developers {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  font-size: 16px;
  font-family: sans-serif, serif;
}

#root {
  width: 100%;
  height: 100%;
}

.main {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  width: 50%;
  display: flex;
  justify-content: space-between;
}

.content {
  width: 100%;
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h2 {
  font-size: 30px;
}

.lang {
  position:absolute;
  top:0;
  right:50px;
}


